@import "Variables";

#root {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.header {
  position: sticky;
  top: 0px;
  height: $layout-panel_height;
  width: 100%;
  background-color: $primary-dark-color;
  display: flex;
  align-items: center;
  z-index: 3;
}

main {
  flex: 1;
}

.nav-menu {
  position: sticky;
  position: -webkit-sticky;
  width: 100%;
  height: $layout-panel_height;
  bottom: 0;
  padding: 0 20px;
  background-color: $primary-dark-color;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__item {
    font-size: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &.active {
      color: #35be74;
      path {
        fill: #35be74 !important;
      }
    }

    &__icon {
      width: 20px;
      height: 20px;
    }
  }
}

.time-travel-alert{
  height: 30px;
  background-color: $secondary-color;
  display: flex;
  justify-content: center;
  align-items: center;
}


$small: 300px;
$medium: 900px;
$large: 1300px;

.contaner {
  margin: 0 auto;
  @media screen and (max-width: $small) {
    max-width:100%
  }
  @media screen and (min-width: $medium) {
  
  }
  @media screen and (min-width: $large) {
    min-width: 800px;

  }
}

@import "AccountSelector";
