main.settings {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;

  .settings-item {
    width: 100%;
    margin: 20px 0 0;

    &__icon {
      width: 36px;
      height: 36px;
      margin: auto;
    }

    .row-item {
      height: 60px;

      &__category-icon {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }

  button {
    max-width: 200px;
    margin-top: 20px;

    &.signout {
      margin: auto 0 30px;
      background-color: $secondary-color;
    }
  }
}
